import styled from 'styled-components';

export const Container = styled.div`
    width: 84vw;
    height: 92vh;
    background-color: #22293d;
`;

export const Header = styled.div`
    display: flex;
    justify-content: space-between;
    margin: 100px;
    button {
        width: 180px;
        height: 50px;
        background-color: #2F9291;
        border: none;
        border-radius: 22px;
        font-size: 1.5em;
        color: white;
    }
    h2 {
        color: #FFFFFF;
    }
`;

export const UploadsBox = styled.div`
    margin-left: 70px;
    display: flex;
    table tr {
        margin: 20px;
    }
    td, th {
        color: white;
        margin-left: 20px;
    }
    td {
        margin: 20px;
    }
    th {
        padding: 20px;
        margin-right: 200px;
    }
    h4 {
        color: white;
        margin-left: 20px;
    }
    img {
        margin: 20px;
        width: 60px;
    }
`;
