import styled from 'styled-components';

export const Container = styled.div`
    width: 84vw;
    height: 92vh;
    background-color: #22293d;
`;

export const Panel = styled.div`
    @media (max-width: 767px) {
        input {
            width: 100px;
        }
        button {
            text-align: center;

        }
    }
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
    h4 {
        color: white;
        font-size: 2em;
        margin: 20px;
        margin-top: 15px;
    }
    input {
        padding: 15px 380px 15px 30px;
        border-radius: 16px;
        border: 1px solid #439D93;
        margin: 10px;
    }
    h6 {
        font-size: 16px;
        margin-left: 320px;
    }
    button {
        margin-top: 40px;
        text-align: center;
        width: 260px;
        height: 44px;
        background-color: #439D93;
        border-radius: 26px;
        border: none;
        color: white;
        font-size: 22px;
    }
`;
