import React from 'react';
import { Form } from '@rocketseat/unform';
import { Container, Panel } from './styles';

const Settings = () => {

    return (
        <Container>
            <Panel>
            <h4>Configurações</h4>
                <input value="bananinha" name="name" type="text" placeholder="Seu nome" />
                <input value="pipoquinha" name="email" type="email" placeholder="Seu email" />
                <input value="bolachinha" name="password" type="password" placeholder="Seu password" />
                <button type="submit">Salvar</button>
            </Panel>
        </Container>
    )
}

export default Settings;