import React from 'react';

import { Container, Header, UploadsBox } from './styles';

const Content = () => {

    return (
        <Container>
            <>
            <Header>
            <h2>Uploads</h2>
                        <button type="submit">New</button>
            </Header>
            <UploadsBox>
            <table>
  <tr>
    <th>file</th>
    <th>filename</th>
    <th>data de upload</th>
  </tr>
  <tr>
    <td><img src="imgs/icones/pdf.png" /></td>
    <td class="a">Pipoquinha da Silva</td>
    <td>03/12/2019</td>
  </tr>
  <tr>
    <td><img src="imgs/icones/jpg.png" /></td>
    <td>Bananinha da Silva</td>
    <td>04/12/2019</td>
  </tr>
</table>
            </UploadsBox>
            </>
        </Container>
    )
}

export default Content;