import React from 'react';
import Sidenav from '../../components/Sidenav';
import { Container } from './styles';
import Header from '../../components/Header';
import Analytics from '../../components/Analytics';

const Dashboard = () => {
    return (
        <>
        <Header />
        <Container>
            <Sidenav />
            <Analytics />
        </Container>
        </>
    )
}

export default Dashboard;