import React from 'react';
import { Container, Panel } from './styles';
import { Link } from 'react-router-dom';

const Sidenav = () => {
    return (
        <Container>
            <Panel>
            <h2 className="fonter"><Link to="/dashboard">Meu Drive</Link></h2>
                <h2 className="fonter"><Link to="/shared">Compartilhado Comigo</Link></h2>
                <h2 className="fonter"><Link to="/analytics">Analytics</Link></h2>
                <h2 className="fonter"><Link to="/settings">Configurações</Link></h2>
            </Panel>
        </Container>
    )
}

export default Sidenav;